@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@media (max-width: 990px) {
  #canvas {
    display: block;
    -webkit-order: 1;
            order: 1;
  }
  #colorPicker1 {
    display: block;
    -webkit-order: 2;
            order: 2;
  }
  #colorPicker2 {
    display: block;
    -webkit-order: 3;
            order: 3;
  }
}

.wrapper {
  text-align: center;
  font-family: "Raleway", sans-serif;
  color: #2c3e50;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.zoom {
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  cursor: pointer;
}
.zoom:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.fs-7 {
  font-size: 1.2rem;
}

.fs-8 {
  font-size: 0.8rem;
}

.scrolller {
  overflow-x: auto;
  white-space: nowrap;
}

.check {
  bottom: 3rem;
  left: 0.5rem;
  position: relative;
}

img {
  height: 9rem;
}


.color {
  position: relative;
  width: 150px;
  height: 100px;
  background: #000;
  margin: 10px auto;
  border-radius: 3px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.15);
  z-index: 2;
}

input[type="range"] {
  -webkit-appearance: none;
  margin: 15px 0;
  width: 200px;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  max-width: 200px;
  height: 5px;
  margin: 0 auto;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  background: #e214f7;
  border-radius: 25px;
  border: 0px solid #000101;
}
input[type="range"]::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 20px;
  width: 20px;
  border-radius: 7px;
  background: #a539e6;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -8px;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #e214f7;
}
input[type="range"]::-moz-range-track {
  max-width: 200px;
  height: 5px;
  margin: 0 auto;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  background: #e214f7;
  border-radius: 25px;
  border: 0px solid #000101;
}
input[type="range"]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 20px;
  width: 20px;
  border-radius: 7px;
  background: #a539e6;
  cursor: pointer;
}
input[type="range"]::-ms-track {
  max-width: 200px;
  height: 5px;
  margin: 0 auto;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 39px 0;
  color: transparent;
}
input[type="range"]::-ms-fill-lower {
  background: #e214f7;
  border: 0px solid #000101;
  border-radius: 50px;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
}
input[type="range"]::-ms-fill-upper {
  background: #e214f7;
  border: 0px solid #000101;
  border-radius: 50px;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
}
input[type="range"]::-ms-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 20px;
  width: 20px;
  border-radius: 7px;
  background: #a539e6;
  cursor: pointer;
}
input[type="range"]:focus::-ms-fill-lower {
  background: #e214f7;
}
input[type="range"]:focus::-ms-fill-upper {
  background: #e214f7;
}

.menu {
  display: -ms-flexbox;
  width: 26px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  cursor: pointer;
}
.menu > span {
  height: 3px;
  border-radius: 3px;
  background-color: #212121;
  background-color: var(--localMenuTextColor, var(--menuTextColor, #212121));
  margin-bottom: 6px;
  display: block;
  transition: all 0.25s ease-in-out;
}
.menu > span:first-child {
  width: 13px;
}
.menu > span:nth-child(2) {
  width: 26px;
}
.menu > span:nth-child(3) {
  width: 18px;
}
.menu:hover > span:first-child {
  width: 26px;
}
.menu:hover > span:nth-child(2) {
  width: 18px;
}
.menu:hover > span:nth-child(3) {
  width: 13px;
}

